const mapStateToProps = state => {
  console.log(state.lang.text);
  var text1;
  var text2;
  var text3;
  var text4;
  var text5;

  switch (state.lang.text) {
    case "KOR":
      text1 = "제품소개.";
      text2 = "회사소개.";
      text3 = "관련기사.";
      text4 = "협력사.";
      text5 = "연락.";

      break;

    case "ENG":
      text1 = "Services.";
      text2 = "Company.";
      text3 = "Press.";
      text4 = "Relations.";
      text5 = "Contact.";

      break;
  }

  return {
    Text1: text1,
    Text2: text2,
    Text3: text3,
    Text4: text4,
    Text5: text5
  };
};

const mapDispatchProps = dispatch => {
  // return bindActionCreators(actions, dispatch);
  return {
    handleSetKOR: text => {
      dispatch(actions.setKOR(text));
    },
    handleSetENG: text => {
      dispatch(actions.setENG(text));
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchProps
)(MainNav);
