import React, { Component } from "react";
import Ontology from "./components/Ontology";
import Compare from "./components/Compare";
import Help from "./components/Help";
import csv from "./components/csv_maker";
import owl_viewer from "./components/owl_viewer"

import "./App.css";
import { Route, HashRouter } from "react-router-dom";

class App extends Component {
  render() {
    return (
      <HashRouter>
        <div>
          
          <Route exact path="/" component={Ontology} />
          <Route path="/Ontology" component={Ontology} />
          <Route path="/Compare" component={Compare} />
          <Route path="/CSV" component={csv} />

          <Route path="/Help" component={Help} />
          <Route path="/owl_viewer" component={owl_viewer} />

        </div>
      </HashRouter>
    );
  }
}

export default App;
